@import 'pre';
$bg: #000;
$baseRotate: 225deg;

body {
  background-color: $bg;
  font-family: Arial, Helvetica, sans-serif;
}

.container{
  max-width: 1000px;
  min-height: 100vh;
  margin: auto;
  //background: linear-gradient( 45deg, purple, cyan);
  
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type='file']{

  &:hover{
    cursor: pointer;
  }

}

img{
  max-width: 100%;
  background-attachment: cover;
  margin: auto;
}

.ruleta{
  width: 500px;
  height: 500px;
  background-color: rgba( 150, 150, 150, .3);
  border-radius: 50%;
  position: relative;
  

  button{
    //background-image: url(./assets/flecha.png);
    position: absolute;
    left: 220px;
    top: 220px;
    height: 60px;
    width: 60px;
    border: none;
    border-radius: 50%;
    color: white;
    transition: .5s ease-out;
    z-index: 1;

    //@debug: yellow;

    &:hover{
      cursor: pointer;
      transform: scale(1.5);
    }
    &:focus{
      outline: none;
    }

  }
}

.item{
  position: absolute;
  overflow: hidden;
  border-radius: 50%;
  &:hover{
    filter: drop-shadow( 0 0 15px #ffff00 );
    //border: 2px solid white;
  }
}

.flecha{
  position: absolute;
  transform: rotate(225deg);
  width: 300px;
  height: 200px;
  left: 100px;
  top: 150px;
  z-index: 0;
  transition: transform 1s;
}

.girar{
  animation: anim 1s linear;
}


@keyframes anim
{
  0%{
    transform:rotateZ(225deg);
  } 
  100%{
    transform:rotateZ(585deg);
  }
}

