$verde: #2af598;
$azul: #009efd;
$purpura: #6a11cb;
$azul_fuerte: #2575fc;
$rojo: #ff0844;
$piel: #ffb199;
$naranja: #f83600;
$amarillo: #f9d423;
$verde_fuerte: #0fd850;
$amarillo_2: #f9f047;

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: arial;
  background: #333;
}

.container{
  min-width: 600px;
  margin: 0 auto;
  background: #222;
  min-height: 100vh;
  
  display: flex;
  align-items: center;
  justify-content: center;
}


.card{
  background: #000;
  height: 400px;
  width: 300px;
  border-radius: 10px;
  padding: 2rem;
  z-index: 0;
}

.glowing{
  position: relative;
  &::before{
    content: '';
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    transition: filter .5s ease-in-out;
    z-index: -1;
  }
  &:hover::before{
    filter: blur(10px);
  }
}

.option{
  color: white;
  border-radius: 5px;
  padding: 1rem;
  margin: 1rem 0;
  &:hover{
    cursor: pointer;
  }

}

.gradiente-agua{
  background: linear-gradient(90deg, $verde 0%, $azul 100%);
  &:before{
    background: linear-gradient(90deg, $verde 0%, $azul 100%);
  }
}
.gradiente-mora{
  background: linear-gradient(90deg, $purpura 0%, $azul_fuerte 100%);
  &:before{
    background: linear-gradient(90deg, $purpura 0%, $azul_fuerte 100%);
  }
}
.gradiente-atardecer{
  background: linear-gradient(90deg, $rojo 0%, $piel 100%);
  &:before{
    background: linear-gradient(90deg, $rojo 0%, $piel 100%);
  }
}
.gradiente-calor{
  background: linear-gradient(90deg, $naranja 0%, $amarillo 100%);
  &:before{
    background: linear-gradient(90deg, $naranja 0%, $amarillo 100%);
  }
}
.gradiente-mango{
  background: linear-gradient(90deg, $verde_fuerte 0%, $amarillo_2 100%);
  &:before{
    background: linear-gradient(90deg, $verde_fuerte 0%, $amarillo_2 100%);
  }
}

.hue{
  animation: hueAnimation 3s  linear infinite;
}

.hover-hue{
  &:hover{
    animation: hueAnimation 3s  linear infinite;
  }
}

@keyframes hueAnimation{
  0%{
    filter: hue-rotate(0deg);
  }
  100%{
    filter: hue-rotate(360deg);
  }
}